import React, {Component} from 'react';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import Footer from "./components/Footer/Footer";
import {BrowserRouter, Route, withRouter} from "react-router-dom";
import LoginPage from "./components/Login/Login";
import {connect, Provider} from "react-redux";
import {compose} from "redux";
import {initializeApp} from "./Redux/app-reduser";
import Preloader from "./components/common/Preloader/Preloader";
import store from "./Redux/redux-store";
import {withSuspense} from "./hoc/withSuspense";
import HeaderContainer from "./components/Header/HeaderContainer";


const DialogsContainer = React.lazy(() => import('./components/Navbar/Dialogs/DialogsContainer'));
const News = React.lazy(() => import('./components/Navbar/News/News'));
const Music = React.lazy(() => import('./components/Navbar/Music/Music'));
const Settings = React.lazy(() => import('./components/Navbar/Settings/Settings'));
const UsersContainer = React.lazy(() => import('./components/Navbar/Users/UsersContainer'));
const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'));
const FriendsContainer = React.lazy(() => import('./components/Navbar/Friends/FriendsContainer'));

class App extends Component {
    componentDidMount() {
        this.props.initializeApp();
    }

    render() {
        if (!this.props.initialized) {
            return <Preloader/>
        }
        return (
            <div className='app-wrapper'>
                <HeaderContainer/>
                <Navbar/>
                <div className='app-wrapper-content'>
                    <Route path='/messages'
                           render={withSuspense(DialogsContainer)}/>
                    <Route path='/profile/:userId?'
                           render={withSuspense(ProfileContainer)}/>
                    <Route path='/users'
                           render={withSuspense(UsersContainer)}/>
                    <Route path='/news' render={withSuspense(News)}/>
                    <Route path='/music' render={withSuspense(Music)}/>
                    <Route path='/settings' render={withSuspense(Settings)}/>
                    <Route path='/friends'
                           render={withSuspense(FriendsContainer)}/>
                    <Route path='/login'
                           render={() => <LoginPage/>}/>
                </div>
                <Footer/>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    initialized: state.app.initialized
})
const AppContainer = compose(
    withRouter,
    connect(mapStateToProps, {initializeApp}))
(App);
const MainApp =(props)=>{
   return <BrowserRouter>
        <Provider store={store}>
            <AppContainer/>
        </Provider>
    </BrowserRouter>
}
export default MainApp;