import React from 'react';
import s from './Navbar.module.css';
import {NavLink} from "react-router-dom";


const Navbar = () => {
    return (<div>
            <nav className={s.nav}>
                <div className={s.item}>
                    <NavLink to='/profile' activeClassName={s.active} type={'text'}>Profile</NavLink>
                </div>
                <div className={s.item}>
                    <NavLink to='/users' activeClassName={s.active} type={'text'}>Users</NavLink>
                </div>
                <div className={s.item}>
                    <NavLink to='/messages' activeClassName={s.active} type={'text'}>Messages</NavLink>
                </div>
                <div className={s.item}>
                    <NavLink to="/news" activeClassName={s.active} type={'text'}>News</NavLink>
                </div>
                <div className={s.item}>
                    <NavLink to="/music" activeClassName={s.active} type={'text'}>Music</NavLink>
                </div>
                <div className={s.item}>
                    <NavLink to="/settings" activeClassName={s.active} type={'text'}>Settings</NavLink>
                </div>
                <div className={s.item}>
                    <div className={s.friends}>
                        <NavLink to="/friends" activeClassName={s.active} type={'text'}>Friends</NavLink>
                        <div>
                            <div className={s.text}><h4>Andrej</h4></div>
                            <img className={s.img}
                                 src={'http://gloria-mur.ru/wp-content/uploads/2017/05/avatar1-740x463.jpg'}/>
                        </div>
                        <div>
                            <div className={s.text}><h4>Sveta</h4></div>
                            <img className={s.img}
                                 src={'http://gloria-mur.ru/wp-content/uploads/2017/05/avatar1-740x463.jpg'}/>
                        </div>
                        <div>
                            <div className={s.text}><h4>Katja</h4></div>
                            <img className={s.img}
                                 src={'http://gloria-mur.ru/wp-content/uploads/2017/05/avatar1-740x463.jpg'}/>
                        </div>
                    </div>
                </div>
            </nav>

        </div>
    )
}

export default Navbar;